import React from 'react';
import { useHistory } from "react-router-dom";
import '@fortawesome/fontawesome-free/css/fontawesome.css'
import '../styles.css';

function Vs({ images, activeId, findFighter, findName, randomId }) {
  const history = useHistory();
  React.useEffect(() => {setTimeout(
    () => {history.push('/mc_choose_hero')}
    , 10000
  )}, [activeId, history])

  let first = (e) => {
    if (e.keyCode === 81) {
      let letters = '0123456789ABCDEF';
      let color = '#';
      for (let i = 0; i < 6; i++) {
        color += letters[Math.floor(Math.random() * 16)];
      }
      return `style="background-color: ${color}"`;
    }
  }

  return (
    <div className="background">
      <div className="left">
        <div className="leftside"></div>
        <div className="left_container">
          <img src="../images/vs_dragon_left.gif" alt="left dragon" className="leftDragon"/>
          <img src={findFighter(images, activeId)} alt={findName(images, activeId)} className="leftfighter" />
        </div>
      </div>
      <div className="vsCenter">
        <div className="vsCenter_upper">
          <img src="../images/Battle_title.gif" alt="Battle" className="battle" />
          <img src="../images/1.jpg" alt="first" className="number" />
          <img src="../images/vs_word.gif" alt="vs" className="vs" />
        </div>
        <div className="vsCenter_lover">
          <img src="../images/fonticons-brands.svg" alt="lov icon" className="lovicon" onKeyUp={(e) => first(e)}/>
          <img src="../images/fonticons-brands.svg" alt="lov icon" className="lovicon"/>
          <img src="../images/fonticons-brands.svg" alt="lov icon" className="lovicon"/>
          <img src="../images/fonticons-brands.svg" alt="lov icon" className="lovicon"/>
          <img src="../images/fonticons-brands.svg" alt="lov icon" className="lovicon"/>
          <img src="../images/fonticons-brands.svg" alt="lov icon" className="lovicon"/>
        </div>
      </div>
      <div className="right">        
        <div className="right_container">
          <img src="../images/vs_dragon_right.gif" alt="right dragon" className="rightDragon"/>
          <img src={randomId(images)} alt={findName(images, activeId)} className="rightfighter" />
        </div>
        <div className="rightside"></div>
      </div>

    </div>
  )
}

export default Vs;